<template><section><html><head></head><body><h1>PostProcessStage</h1>
<p>The <code v-pre="">vc-stage-process-post</code> component is used to run a post-process stage on either the texture rendered by the scene or the output of a previous post-process stage.</p>
<h2>Example</h2>
<h3>Load a PostProcessStage</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-stage-process-post :fragment-shader="fragmentShader"></vc-stage-process-post>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>Code</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-stage-process-post</span> <span class="hljs-attr">:fragment-shader</span>=<span class="hljs-string">"fragmentShader"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-stage-process-post</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">fragmentShader</span>: <span class="hljs-string">`
          uniform sampler2D colorTexture;
          varying vec2 v_textureCoordinates;
          float hash(float x){
            return fract(sin(x*23.3)*13.13);
          }
          void main(void){
            float time = czm_frameNumber / 60.0;
            vec2 resolution = czm_viewport.zw;
            vec2 uv=(gl_FragCoord.xy*2.-resolution.xy)/min(resolution.x,resolution.y);
            vec3 c=vec3(.6,.7,.8);
            float a=-.4;
            float si=sin(a),co=cos(a);
            uv*=mat2(co,-si,si,co);
            uv*=length(uv+vec2(0,4.9))*.3+1.;
            float v=1.-sin(hash(floor(uv.x*100.))*2.);
            float b=clamp(abs(sin(20.*time*v+uv.y*(5./(2.+v))))-.95,0.,1.)*20.;
            c*=v*b;
            gl_FragColor = mix(texture2D(colorTexture, v_textureCoordinates), vec4(c,1), 0.5);
          }  `</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span> = cesiumInstance
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>fragmentShader</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> The fragment shader to use. The default sampler2D uniforms are colorTexture and depthTexture.</td>
</tr>
<tr>
<td>uniforms</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> An object whose properties will be used to set the shaders uniforms.</td>
</tr>
<tr>
<td>textureScale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A number in the range (0.0, 1.0] used to scale the texture dimensions. A scale of 1.0 will render this post-process stage to a texture the size of the viewport.</td>
</tr>
<tr>
<td>forcePowerOfTwo</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Whether or not to force the texture dimensions to be both equal powers of two. The power of two will be the next power of two of the minimum of the dimensions.</td>
</tr>
<tr>
<td>sampleMode</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> How to sample the input color texture. <strong>{NEAREST: 0, LINEAR: 1}</strong></td>
</tr>
<tr>
<td>pixelFormat</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The color pixel format of the output texture.</td>
</tr>
<tr>
<td>pixelDatatype</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The pixel data type of the output texture.</td>
</tr>
<tr>
<td>clearColor</td>
<td>Object|Array|String</td>
<td><code v-pre="">BLACK</code></td>
<td><code v-pre="">optional</code> The color to clear the output texture to.</td>
</tr>
<tr>
<td>scissorRectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The rectangle to use for the scissor test.</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> The unique name of this post-process stage for reference by other stages in a composite. If a name is not supplied, a GUID will be generated.</td>
</tr>
